import { Form } from "formik";
import React from "react";
import { PrimaryButton, SecondaryButton } from "./Buttons";

export const BaseForm: React.FC<{}> = (props): JSX.Element => (
  <Form className="space-y-8">{props.children}</Form>
);

export const FormSection: React.FC<{}> = (props): JSX.Element => (
  <div>{props.children}</div>
);

export const FormSectionHeading: React.FC<{
  title: string;
  description?: string;
}> = (props): JSX.Element => (
  <div>
    <h3 className="text-md mt-6 font-medium leading-6 text-gray-900">
      {props.title}
    </h3>
    {props.description && (
      <p className="mt-1 text-sm text-gray-500">{props.description}</p>
    )}
  </div>
);

/**
 * FormLabel which displays as a block above the input field.
 */
export const FormLabel: React.FC<{}> = (props): JSX.Element => (
  <label className="block truncate text-sm font-medium leading-5 text-gray-700">
    {props.children}
  </label>
);

export const SaveCancel: React.FC<{
  isSubmitting: boolean;
  close: () => void;
}> = (props): JSX.Element => (
  <div className="pt-5">
    <div className="flex justify-end">
      <SecondaryButton
        type="button"
        disabled={props.isSubmitting}
        onClick={props.close}
      >
        Cancel
      </SecondaryButton>
      <PrimaryButton type="submit">Save</PrimaryButton>
    </div>
  </div>
);
