import { Formik } from "formik";
import { err, ok, Result } from "neverthrow";
import React from "react";
import useSWR, { useSWRConfig } from "swr";
import { ACCOUNT } from "../../../cacheKeys";
import { supabase } from "../../../supabase";
import { Alert } from "../../elements/Alert";
import { ErrorOrLoadingWrapper } from "../../elements/ErrorLoadingWrapper";
import { TextField } from "../../elements/fields/TextField";
import { BaseForm, FormSection } from "../../elements/Form";
import { PageHeading } from "../../elements/PageHeading";

type AccountDetails = {
  id: string; // user_id
  username: string;
  avatar_url: string;
};

type AccountValue = {
  username: string;
};

const getAccountDetails = async (
  cacheKey: string,
  userID: string
): Promise<AccountDetails | undefined> =>
  supabase
    .from<AccountDetails>("profiles")
    .select("username, avatar_url")
    .eq("id", userID)
    .throwOnError()
    .then(({ data }) =>
      data && data.length > 0
        ? {
            id: userID,
            username: data[0].username,
            avatar_url: data[0].avatar_url,
          }
        : undefined
    );

export const Account: React.FC<{
  userID: string;
  newAlert: (newAlert: Alert) => void;
}> = (props): JSX.Element => {
  const { mutate } = useSWRConfig();
  const { data, error } = useSWR([ACCOUNT, props.userID], getAccountDetails);

  const updateProfile = async (
    values: AccountValue
  ): Promise<Result<void, string>> => {
    const updates = {
      id: props.userID,
      username: values.username,
      updated_at: new Date(),
    };
    let { error } = await supabase
      .from("profiles")
      .upsert(updates, { returning: "minimal" });

    if (error) {
      console.error(error);
      return err("Failed to update profile");
    }
    return ok(undefined);
  };

  return (
    <>
      <PageHeading>Account Details</PageHeading>

      <ErrorOrLoadingWrapper error={error} data={data}>
        {(innerData) => (
          <Formik
            initialValues={{
              username: innerData.username,
            }}
            onSubmit={async (values, { setSubmitting }) => {
              const res = await updateProfile(values);
              setSubmitting(false);

              res.match(
                () => {
                  props.newAlert({
                    level: "success",
                    message: "Successfully updated profile",
                    timeout: 3,
                  });

                  // Refresh the displayed tasks
                  mutate(ACCOUNT);
                },
                (err) => {
                  props.newAlert({ level: "error", message: err, timeout: 3 });
                }
              );
            }}
          >
            {(formik) => (
              <BaseForm>
                <FormSection>
                  <TextField label="Username" name="username" />
                </FormSection>

                <div className="pt-5">
                  <div className="flex justify-end">
                    <button
                      type="submit"
                      disabled={formik.isSubmitting}
                      className="ml-3 inline-flex justify-center rounded-md border border-transparent bg-indigo-600 py-2 px-4 text-sm font-medium text-white shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
                    >
                      Save
                    </button>
                  </div>
                </div>
              </BaseForm>
            )}
          </Formik>
        )}
      </ErrorOrLoadingWrapper>

      <div>
        <button onClick={() => supabase.auth.signOut()}>Sign Out</button>
      </div>
    </>
  );
};
