import {
  POINTS,
  REWARDS,
  TASKS_BASE,
  TASKS_COMPLETION,
  TASKS_PERIOD,
} from "../../../cacheKeys";
import { ScopedMutator } from "swr/dist/types";

// TODO: Investigate enhancing get___Tasks functions to also have a version which refreshes a single tasks.
// If successful, apply to other resource types as well.

type TaskBase = {
  id: number;
  name: string;
  description: string;
  checkable_by_sub: boolean;
  tasks_log: {
    // Will be sorted from earliest to latest
    created_at: string;
    parsed_created_at: Date;
  }[];
};

type CompletionTaskRule = {
  id: number;
  task_id: number;
  points: {
    id: number;
    shape: string;
    color: string;
    value: number;
  };
  streak_modifier: number;
  streak_max_value?: number;
  value: number;
};

type PeriodTaskRule = {
  id: number;
  task_id: number;
  if_completed: boolean;
  at_least: boolean;
  count: number;
  period: "day" | "week" | "fortnight" | "month";
  value: number;
  points: {
    id: number;
    shape: string;
    color: string;
    value: number;
  };
};

type CompletionTask = TaskBase & {
  type: "completion";
  rules: CompletionTaskRule[];
};

type PeriodTask = TaskBase & {
  type: "period";
  rules: PeriodTaskRule[];
};

const getPeriodTasks = async (cacheKey: string): Promise<PeriodTaskRule[]> =>
  supabase
    .from<PeriodTaskRule>("task_period_rules")
    .select(
      "id, task_id, if_completed, at_least, count, period, value, points(id, shape, color, value)"
    )
    .throwOnError()
    .then(({ data }) => data || []);

const getCompletionTasks = async (
  cacheKey: string
): Promise<CompletionTaskRule[]> =>
  supabase
    .from<CompletionTaskRule>("task_completion_rules")
    .select(
      "id, task_id, streak_modifier, streak_max_value, value, points(id, shape, color, value)"
    )
    .throwOnError()
    .then(({ data }) => data || []);

const getBaseTasks = async (cacheKey: string): Promise<TaskBase[]> =>
  supabase
    .from<TaskBase>("tasks_base")
    .select("id, name, description, checkable_by_sub, tasks_log(created_at)")
    .throwOnError()
    .then(({ data }) => data || [])
    .then((data) => {
      data.forEach((task) => {
        task.tasks_log.forEach((log) => {
          // Timestamps are in the format 2022-02-23T16:10:26.000Z
          log.parsed_created_at = parseTimestamp(log.created_at);
        });
        task.tasks_log.sort(
          (a, b) =>
            a.parsed_created_at.getTime() - b.parsed_created_at.getTime()
        );
      });
      return data;
    });

/**
 * useCompletionTasks wraps the retrieval of base tasks and the completion
 * rules, to return only base tasks which have at least one completion rule.
 */
const useCompletionTasks = (): {
  data: CompletionTask[] | undefined;
  error: any;
} => {
  const { data: tasks, error: errorTasks } = useSWR(TASKS_BASE, getBaseTasks);
  const { data: rules, error: errorRules } = useSWR(
    TASKS_COMPLETION,
    getCompletionTasks
  );

  if (errorTasks) {
    return { data: undefined, error: errorTasks };
  } else if (errorRules) {
    return { data: undefined, error: errorRules };
  }

  if (!tasks || !rules) {
    return { data: undefined, error: undefined };
  }

  return {
    data: tasks
      .map(
        (task): CompletionTask => ({
          ...task,
          type: "completion",
          rules: rules.filter((rule) => rule.task_id === task.id),
        })
      )
      .filter((task) => task.rules.length > 0),
    error: undefined,
  };
};

// TODO: PeriodTasks will need to have their history carefully evaluated.
// If a user has not logged in for a while, we need to evaluate all the rules
// back through history. However, if both the dom and sub log in at the same
// time we need to ensure that the rules are not evaluated multiple times.

/**
 * usePeriodTasks wraps the retrieval of base tasks and the period rules, to
 * return only base tasks which have at least one period rule.
 */
const usePeriodTasks = (): {
  data: PeriodTask[] | undefined;
  error: any;
} => {
  // TODO: Can this be made generic with useCompletionTasks?

  const { data: tasks, error: errorTasks } = useSWR(TASKS_BASE, getBaseTasks);
  const { data: rules, error: errorRules } = useSWR(
    TASKS_PERIOD,
    getPeriodTasks
  );

  if (errorTasks) {
    return { data: undefined, error: errorTasks };
  } else if (errorRules) {
    return { data: undefined, error: errorRules };
  }

  if (!tasks || !rules) {
    return { data: undefined, error: undefined };
  }

  return {
    data: tasks
      .map(
        (task): PeriodTask => ({
          ...task,
          type: "period",
          rules: rules.filter((rule) => rule.task_id === task.id),
        })
      )
      .filter((task) => task.rules.length > 0),
    error: undefined,
  };
};

const useAllTasks = (): {
  data: (PeriodTask | CompletionTask)[] | undefined;
  error: any;
} => {
  const { data: periodTasks, error: errorPeriod } = usePeriodTasks();
  const { data: completionTasks, error: errorCompletion } =
    useCompletionTasks();

  if (errorPeriod) {
    return { data: undefined, error: errorPeriod };
  } else if (errorCompletion) {
    return { data: undefined, error: errorCompletion };
  }

  return {
    data: [...(periodTasks || []), ...(completionTasks || [])].sort(
      (a, b) => a.id - b.id
    ),
    error: undefined,
  };
};

export const Tasks: React.FC<{
  partnershipID: number;
  newAlert: (newAlert: Alert) => void;
}> = (props): JSX.Element => {
  // NOTE: Table policies for periodTasks should allow either sub or dom to
  // update the history, but only allow dom to check the task off if configured
  // that way. This is required so that if the dom has not logged in for a
  // while, the points are still reflected accurately.

  const { data: tasks, error } = useAllTasks();
  const [showCreate, setShowCreate] = React.useState(false);

  return (
    <>
      <PageHeading>Tasks</PageHeading>

      {/* TODO: Have a nice empty state when there are no tasks */}
      <ErrorOrLoadingWrapper data={tasks} error={error}>
        {(innerData) =>
          innerData.length > 0 ? (
            <div className="py-4">
              <Grid
                component={TaskCard}
                newAlert={props.newAlert}
                data={innerData}
              />
            </div>
          ) : (
            <p>No tasks</p>
          )
        }
      </ErrorOrLoadingWrapper>

      {/* TODO: Hide this for subs */}
      <AddOrbAndFlyover
        title="Create Task"
        show={showCreate}
        setShow={setShowCreate}
      >
        <CreateTask
          partnershipID={props.partnershipID}
          newAlert={props.newAlert}
          close={() => {
            setShowCreate(false);
          }}
        />
      </AddOrbAndFlyover>
    </>
  );
};

