const markTaskComplete = async (
  task: CompletionTask | PeriodTask,
  newAlert: (newAlert: Alert) => void,
  mutate: ScopedMutator
): Promise<void> => {
  if (task.type === "period") {
    console.error("Period-based tasks are not supported yet");
    return;
  }

  let prevDay = trimTimestampToDay(new Date());
  let streak = 0;
  loop: for (let i = task.tasks_log.length - 1; i >= 0; i--) {
    const currentDay = trimTimestampToDay(task.tasks_log[i].parsed_created_at);
    switch (daysBetween(currentDay, prevDay)) {
      case 1:
        streak++;
        prevDay = currentDay;
        continue loop;
      case 0:
        continue loop;
      default:
        break loop;
    }
  }

  const promises: PromiseLike<boolean>[] = [];

  task.rules.forEach((rule) => {
    let modifier = streak * rule.streak_modifier;
    if (rule.streak_max_value) {
      modifier = Math.min(modifier, rule.streak_max_value);
    }
    const newValue = rule.points.value + rule.value + modifier;

    promises.push(
      supabase
        .from("points_log")
        .insert(
          { points_id: rule.points.id, value: newValue },
          { returning: "minimal" }
        )
        .then(({ error }) => {
          if (error) {
            console.error("Unable to update points log");
            console.error(error);
            return false;
          }
          return true;
        })
    );

    // TODO: Prevent a task from updating the same points type with multiple rules.
    promises.push(
      supabase
        .from("points")
        .update({ value: newValue }, { returning: "minimal" })
        .match({ id: rule.points.id })
        .then(({ error }) => {
          if (error) {
            console.error("Unable to update points value");
            console.error(error);
            return false;
          }
          return true;
        })
    );
  });

  promises.push(
    supabase
      .from("tasks_log")
      .insert({ task_id: task.id }, { returning: "minimal" })
      .then(({ error }) => {
        if (error) {
          console.error("Unable to update tasks log");
          console.error(error);
          return false;
        }
        return true;
      })
  );

  const anyErrors = (await Promise.all(promises)).some((v) => !v);
  if (anyErrors) {
    newAlert({ level: "error", message: "Unable to mark task as complete" });
  } else {
    newAlert({ level: "success", message: "Task completed" });
  }

  mutate(POINTS);
  mutate(REWARDS);
  mutate(TASKS_BASE);
  mutate(TASKS_COMPLETION);
};

/**
 * parseTimestamp expects the timestamp to be formated 2022-02-23T16:10:26.000Z
 */
const parseTimestamp = (ts: string): Date => {
  const b = ts.split(/\D/).map((part) => parseInt(part));
  let [year, month, day, hour, minute, second, ms, _] = b;
  return new Date(Date.UTC(year, month - 1, day, hour, minute, second, ms));
};

const trimTimestampToDay = (date: Date): Date =>
  new Date(date.getFullYear(), date.getMonth(), date.getDate());

const daysBetween = (a: Date, b: Date): number =>
  (b.getTime() - a.getTime()) / (24 * 60 * 60 * 1000);
