import { RadioGroup } from "@headlessui/react";
import { useField } from "formik";
import React from "react";
import { classNames } from "../../../css";
import { DEFAULT_NAME, Field, ViewProps } from "./Field";

export type RadioOption = {
  name: string;
  enabled: boolean;
  selectedClassNames?: string;
  deselectedClassNames?: string;
};

export const SmallCardsRadioField: React.FC<
  {
    options: RadioOption[];
  } & ViewProps
> = ({ options, ...fieldProps }): JSX.Element => {
  const [, meta, helpers] = useField<string>(fieldProps.name || DEFAULT_NAME);

  return (
    <Field {...fieldProps}>
      {(renderProps) => (
        <RadioGroup
          {...renderProps}
          value={meta.value}
          onChange={(v) => helpers.setValue(v)}
          className="mt-2"
        >
          <div className="grid grid-cols-5 gap-3">
            {options.map((option) => {
              return (
                <RadioGroup.Option
                  key={option.name}
                  value={option.name}
                  className={({ active, checked }) =>
                    classNames(
                      option.enabled
                        ? "cursor-pointer focus:outline-none"
                        : "cursor-not-allowed opacity-25",
                      active ? "ring-2 ring-indigo-500 ring-offset-2" : "",
                      checked
                        ? `border-transparent text-white`
                        : `border-gray-200 text-gray-900`,
                      checked
                        ? option.selectedClassNames ||
                            "bg-indigo-600 hover:bg-indigo-700"
                        : option.deselectedClassNames ||
                            "bg-white hover:bg-gray-50",
                      "flex items-center justify-center rounded-md border py-3 px-3 text-sm font-medium uppercase sm:flex-1"
                    )
                  }
                  disabled={!option.enabled}
                >
                  <RadioGroup.Label as="p">{option.name}</RadioGroup.Label>
                </RadioGroup.Option>
              );
            })}
          </div>
        </RadioGroup>
      )}
    </Field>
  );
};
