import { createClient, Session, User } from "@supabase/supabase-js";
import useSWR from "swr";
import { LOGIN } from "./cacheKeys";

const apiURL = "https://vayobllbooqzursfposo.supabase.co";
const apiKey =
  "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJvbGUiOiJhbm9uIiwiaWF0IjoxNjQzMDQzOTg0LCJleHAiOjE5NTg2MTk5ODR9.YpCtRHCNmkZFzdWFwoz8mYgHdnfzo3x8m1CHM2IeB-M";

export const supabase = createClient(apiURL, apiKey, {
  autoRefreshToken: true,
  persistSession: true,
});

export type SupaSession = {
  error?: string;
  session?: Session;
  user?: User;
};

export const useSession = (): SupaSession => {
  const { data: session, error } = useSWR(LOGIN, () => supabase.auth.session());

  if (error) {
    console.error("Failed to retrieve session");
    console.error(error);
  }

  return {
    error,
    session: session || undefined,
    user: session?.user || undefined,
  };
};
