import React from "react";
import useSWR, { mutate } from "swr";
import { POINTS, REWARDS } from "../../../cacheKeys";
import { classNames } from "../../../css";
import { supabase } from "../../../supabase";
import { AddOrbAndFlyover } from "../../elements/AddOrbAndFlyover";
import { Alert } from "../../elements/Alert";
import { ErrorOrLoadingWrapper } from "../../elements/ErrorLoadingWrapper";
import { Grid } from "../../elements/Grid";
import { PageHeading } from "../../elements/PageHeading";
import { Tabs } from "../../elements/Tabs";
import { CreatePointType } from "../points/CreatePoints";
import { PointIcon } from "../points/PointIcon";
import { Points } from "../points/Points";
import { CreateReward } from "./CreateReward";

type Reward = {
  id: number;
  name: string;
  description: string;
  is_reward: boolean;
  point_modifier: number;
  points: {
    id: number;
    shape: string;
    color: string;
    value: number;
  };
};

const getRewards = async (cacheKey: string): Promise<Reward[]> =>
  supabase
    .from<Reward>("outcomes")
    .select(
      "id, name, description, point_modifier, points(id, shape, color, value)"
    )
    .eq("is_reward", true)
    .throwOnError()
    .then(({ data }) => data || []);

export const PointsAndRewards: React.FC<{
  partnershipID: number;
  newAlert: (newAlert: Alert) => void;
}> = (props): JSX.Element => {
  const [showCreate, setShowCreate] = React.useState(false);

  return (
    <>
      <Points partnershipID={props.partnershipID} newAlert={props.newAlert} />
      <Rewards partnershipID={props.partnershipID} newAlert={props.newAlert} />

      {/* TODO: Hide this for subs */}
      <AddOrbAndFlyover
        title="Create Rewards or Points"
        show={showCreate}
        setShow={setShowCreate}
      >
        <CreateRewardOrPoint
          partnershipID={props.partnershipID}
          newAlert={props.newAlert}
          close={() => {
            setShowCreate(false);
          }}
        />
      </AddOrbAndFlyover>
    </>
  );
};

const Rewards: React.FC<{
  partnershipID: number;
  newAlert: (newAlert: Alert) => void;
}> = (props): JSX.Element => {
  const { data: rewards, error } = useSWR(REWARDS, getRewards);

  return (
    <>
      <PageHeading>Rewards</PageHeading>

      {/* TODO: Have a nice empty state when there are no tasks */}
      <ErrorOrLoadingWrapper data={rewards} error={error}>
        {(innerData) =>
          innerData.length > 0 ? (
            <div className="py-4">
              <Grid
                component={RewardCard}
                newAlert={props.newAlert}
                data={innerData}
              />
            </div>
          ) : (
            <p>No points</p>
          )
        }
      </ErrorOrLoadingWrapper>
    </>
  );
};

const RewardCard: React.FC<{
  data: Reward;
  newAlert: (newAlert: Alert) => void;
}> = (props): JSX.Element => {
  const canAfford = props.data.point_modifier * -1 <= props.data.points.value;

  const redeemReward = async () => {
    const { error } = await supabase
      .from("outcomes_log")
      .insert(
        { created_at: new Date(), outcome_id: props.data.id },
        { returning: "minimal" }
      );

    if (error) {
      console.error(error);
      props.newAlert({
        message: "Failed to redeem reward",
        level: "error",
      });
      return;
    }

    const newValue = props.data.points.value + props.data.point_modifier;
    const { error: error2 } = await supabase
      .from("points")
      .update({ value: newValue }, { returning: "minimal" })
      .eq("id", props.data.points.id);

    if (error2) {
      // TODO: Should roll back outcomes_log modification
      console.error(error2);
      props.newAlert({
        message: "Failed to redeem reward",
        level: "error",
      });
      return;
    }

    mutate(REWARDS);
    mutate(POINTS);
    props.newAlert({
      message: "Reward redeemed!",
      level: "success",
    });
  };

  return (
    <div className="flex w-full flex-row divide-x divide-gray-200">
      <div className="flex w-full items-center justify-between space-x-6 p-6">
        <h3 className="text-md truncate font-medium text-gray-900">
          {props.data.name}
        </h3>
        <div>
          <button
            type="button"
            onClick={() => {
              canAfford
                ? redeemReward()
                : props.newAlert({
                    message: "Not enough points for that reward",
                    level: "info",
                  });
            }}
            className={classNames(
              "inline-flex items-center rounded-md border border-transparent px-3 py-2 align-middle text-sm font-medium leading-4 shadow-sm ",
              canAfford
                ? "cursor-pointer bg-slate-200 text-gray-900 hover:bg-slate-400 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
                : "cursor-not-allowed bg-gray-200 text-gray-500"
            )}
          >
            Buy for {props.data.point_modifier * -1}
            <PointIcon
              className="ml-2 h-4 h-min w-4"
              shape={props.data.points.shape}
              color={props.data.points.color}
            />
          </button>
        </div>
      </div>
    </div>
  );
};

const CreateRewardOrPoint: React.FC<{
  partnershipID: number;
  newAlert: (newAlert: Alert) => void;
  close: () => void;
}> = (props): JSX.Element => {
  const [activeTab, setActiveTab] = React.useState("New Reward");

  return (
    <>
      <Tabs
        tabs={["New Reward", "New Point Type"]}
        activeTab={activeTab}
        setActiveTab={setActiveTab}
      />
      {activeTab === "New Reward" && (
        <CreateReward
          partnershipID={props.partnershipID}
          newAlert={props.newAlert}
          close={props.close}
        />
      )}
      {activeTab === "New Point Type" && (
        <CreatePointType
          partnershipID={props.partnershipID}
          newAlert={props.newAlert}
          close={props.close}
        />
      )}
    </>
  );
};
