import { Formik } from "formik";
import { err, ok, Result } from "neverthrow";
import { useSWRConfig } from "swr";
import { POINTS } from "../../../cacheKeys";
import { supabase } from "../../../supabase";
import { Alert } from "../../elements/Alert";
import { SmallCardsRadioField } from "../../elements/fields/SmallCardsRadioField";
import { TextField } from "../../elements/fields/TextField";
import { SaveCancel, BaseForm, FormSection } from "../../elements/Form";

type PointTypeValues = {
  name: string;
  color: string;
  partnership_id: number;
};

export const CreatePointType: React.FC<{
  partnershipID: number;
  newAlert: (newAlert: Alert) => void;
  close: () => void;
}> = (props): JSX.Element => {
  const { mutate } = useSWRConfig();

  const saveNewPointType = async (
    values: PointTypeValues
  ): Promise<Result<void, string>> => {
    let { error } = await supabase
      .from("points")
      .insert(values, { returning: "minimal" });
    if (error) {
      console.error(error);
      return err("Failed to create new point type");
    }
    return ok(undefined);
  };

  const initialValues: PointTypeValues = {
    name: "",
    color: "",
    partnership_id: props.partnershipID,
  };

  return (
    <Formik
      initialValues={initialValues}
      onSubmit={async (values, { setSubmitting }) => {
        const res = await saveNewPointType(values);
        setSubmitting(false);

        // TODO: Validate that a name and color are set

        res.match(
          () => {
            props.newAlert({
              level: "success",
              message: "Successfully created point type",
              timeout: 3,
            });

            // Refresh the displayed points
            mutate(POINTS);
          },
          (err) => {
            props.newAlert({ level: "error", message: err, timeout: 3 });
          }
        );

        props.close();
      }}
    >
      {(formik) => (
        <BaseForm>
          <FormSection>
            <TextField name="name" label="Name" />

            <SmallCardsRadioField
              name="color"
              label="Color"
              options={[
                {
                  name: "blue",
                  enabled: true,
                  selectedClassNames: "bg-blue-500 hover:bg-blue-600",
                  deselectedClassNames: "bg-blue-200 hover:bg-blue-500",
                },
                {
                  name: "green",
                  enabled: true,
                  selectedClassNames: "bg-green-500 hover:bg-green-600",
                  deselectedClassNames: "bg-green-200 hover:bg-green-500",
                },
                {
                  name: "red",
                  enabled: true,
                  selectedClassNames: "bg-red-500 hover:bg-red-600",
                  deselectedClassNames: "bg-red-200 hover:bg-red-500",
                },
                {
                  name: "yellow",
                  enabled: true,
                  selectedClassNames: "bg-yellow-500 hover:bg-yellow-600",
                  deselectedClassNames: "bg-yellow-100 hover:bg-yellow-500",
                },
              ]}
            />
          </FormSection>

          <SaveCancel isSubmitting={formik.isSubmitting} close={props.close} />
        </BaseForm>
      )}
    </Formik>
  );
};
