import { Formik } from "formik";
import React from "react";
import { supabase } from "../../../supabase";
import { CenteredForm } from "../../CenteredForm";
import { PrimaryButton } from "../../elements/Buttons";
import { EmailField } from "../../elements/fields/EmailField";
import { BaseForm, FormSection } from "../../elements/Form";

export const ForgotPassword: React.FC<{}> = (props) => {
  const [message, setMessage] = React.useState("");

  return (
    <CenteredForm
      header={
        <>
          <img
            className="mx-auto h-12 w-auto"
            src="/logo512-nobg.png"
            alt="HappySub"
          />
          <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
            Reset your password
          </h2>
          <p className="mt-2 text-center text-sm text-gray-600">
            Enter your email and we'll send you a link to reset your password.
          </p>
        </>
      }
    >
      <Formik
        initialValues={{ email: "" }}
        onSubmit={async (values) => {
          const { error } = await supabase.auth.api.resetPasswordForEmail(
            values.email
          );
          if (error) {
            console.error(error);
            setMessage("Failed to set password");
          } else {
            setMessage("Please check your email");
          }
        }}
      >
        {(formik) => (
          <BaseForm>
            <FormSection>
              <EmailField name="email" label="Email address" />
            </FormSection>

            {message && <p>{message}</p>}

            <div>
              <PrimaryButton
                disabled={formik.isSubmitting}
                className="w-full"
                type="submit"
              >
                Send Reset Email
              </PrimaryButton>
            </div>
          </BaseForm>
        )}
      </Formik>
    </CenteredForm>
  );
};
