import { Formik } from "formik";
import React from "react";
import { supabase, useSession } from "../../../supabase";
import { CenteredForm } from "../../CenteredForm";
import { PrimaryButton } from "../../elements/Buttons";
import { PasswordField } from "../../elements/fields/PasswordField";
import { BaseForm, FormSection } from "../../elements/Form";

export const NewPassword: React.FC<{}> = (props) => {
  const session = useSession();
  const [message, setMessage] = React.useState("");

  const innerSession = session.session;
  if (!innerSession || !innerSession.access_token) {
    return <p>Missing access token</p>;
  }

  return (
    <CenteredForm
      header={
        <>
          <img
            className="mx-auto h-12 w-auto"
            src="/logo512-nobg.png"
            alt="HappySub"
          />
          <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
            Password Reset
          </h2>
          <p className="mt-2 text-center text-sm text-gray-600">
            Please enter a new password.
          </p>
        </>
      }
    >
      <Formik
        initialValues={{ password: "" }}
        onSubmit={async (values) => {
          const { error } = await supabase.auth.api.updateUser(
            innerSession.access_token,
            { password: values.password }
          );
          if (error) {
            console.error(error);
            setMessage("Failed to reset password");
          } else {
            setMessage("Password updated");
            // TODO: Redirect to another page afterwards
          }
        }}
      >
        {(formik) => (
          <BaseForm>
            <FormSection>
              <PasswordField name="password" label="New Password" />
            </FormSection>

            {message && <p>{message}</p>}

            <div>
              <PrimaryButton
                disabled={formik.isSubmitting}
                className="w-full"
                type="submit"
              >
                Set Password
              </PrimaryButton>
            </div>
          </BaseForm>
        )}
      </Formik>
    </CenteredForm>
  );
};
