import { FieldArray, Formik } from "formik";
import { err, ok, Result } from "neverthrow";
import React from "react";
import { Alert } from "../../elements/Alert";
import {
  SaveCancel,
  BaseForm,
  FormSection,
  FormSectionHeading,
} from "../../elements/Form";
import { TextField } from "../../elements/fields/TextField";
import { useSWRConfig } from "swr";
import { supabase } from "../../../supabase";
import { TASKS_BASE } from "../../../cacheKeys";
import { ToggleField } from "../../elements/fields/ToggleField";

type CompletionRule = {
  point_id: number;
  enable_streak: boolean;
  streak_modifier: number;
  streak_max_value?: number;
  value: number;
};

type TaskValues = {
  name: string;
  description: string;
  checkable_by_sub: boolean;
  partnership_id: number;
  completion_rules: CompletionRule[];
};

export const CreateTask: React.FC<{
  partnershipID: number;
  newAlert: (newAlert: Alert) => void;
  close: () => void;
}> = (props): JSX.Element => {
  const { mutate } = useSWRConfig();

  const saveNewTask = async (
    values: TaskValues
  ): Promise<Result<void, string>> => {
    if (true) {
      console.log(values);
      return ok(undefined);
    }

    let { error } = await supabase
      .from("tasks")
      .insert(values, { returning: "minimal" });
    if (error) {
      console.error(error);
      return err("Failed to create new task");
    }
    return ok(undefined);
  };

  const initialValues: TaskValues = {
    name: "",
    description: "",
    checkable_by_sub: false,
    partnership_id: props.partnershipID,
    completion_rules: [
      {
        point_id: 1,
        enable_streak: false,
        streak_modifier: 0,
        value: 1,
      },
      {
        point_id: 1,
        enable_streak: false,
        streak_modifier: 0,
        value: 1,
      },
    ],
  };

  return (
    <Formik
      initialValues={initialValues}
      onSubmit={async (values, { setSubmitting }) => {
        const res = await saveNewTask(values);
        setSubmitting(false);

        res.match(
          () => {
            props.newAlert({
              level: "success",
              message: "Successfully created task",
              timeout: 3,
            });

            // Refresh the displayed tasks
            // TODO: Also mutate TASKS_PERIOD or TASKS_COMPLETION
            mutate(TASKS_BASE);
          },
          (err) => {
            props.newAlert({ level: "error", message: err, timeout: 3 });
          }
        );

        props.close();
      }}
    >
      {(formik) => (
        <BaseForm>
          <FormSection>
            <TextField label="Name" name="name" />

            <TextField label="Description" name="description" />

            <ToggleField
              inlineLabel="Submissive can mark done"
              name="checkable_by_sub"
            />
          </FormSection>

          <FormSection>
            <FormSectionHeading title="When complete" />

            <FieldArray
              name="completion_rules"
              render={(arrayHelpers) => (
                <ul role="list">
                  {formik.values.completion_rules.map((rule, i) => (
                    <li key={i} className="bg-white-200 my-4 py-4 shadow-md">
                      <p>Testing 2</p>
                    </li>
                  ))}
                </ul>
              )}
            />
          </FormSection>

          <SaveCancel isSubmitting={formik.isSubmitting} close={props.close} />
        </BaseForm>
      )}
    </Formik>
  );
};
