import { Formik } from "formik";
import { ok, err, Result } from "neverthrow";
import React from "react";
import useSWR, { useSWRConfig } from "swr";
import { POINTS, REWARDS } from "../../../cacheKeys";
import { supabase } from "../../../supabase";
import { Alert } from "../../elements/Alert";
import { ErrorOrLoadingWrapper } from "../../elements/ErrorLoadingWrapper";
import { NumberField } from "../../elements/fields/NumberField";
import { SelectPointTypeField } from "../../elements/fields/SelectPointTypeField";
import { TextField } from "../../elements/fields/TextField";
import {
  SaveCancel,
  BaseForm,
  FormSection,
  FormLabel,
} from "../../elements/Form";
import { getPoints } from "../points/Points";

type RewardValues = {
  name: string;
  description: string;
  point_modifier: number;
  points_id: number;
  partnership_id: number;
};

export const CreateReward: React.FC<{
  partnershipID: number;
  newAlert: (newAlert: Alert) => void;
  close: () => void;
}> = (props): JSX.Element => {
  const { mutate } = useSWRConfig();
  const { data: points, error } = useSWR(POINTS, getPoints);

  const saveNewReward = async (
    values: RewardValues
  ): Promise<Result<void, string>> => {
    // The point modifier is presented as "cost" in the UI, so it's the inverse
    // of how we use it in the database.
    const valuesToSend = {
      ...values,
      point_modifier: values.point_modifier * -1,
    };

    let { error } = await supabase
      .from("outcomes")
      .insert(valuesToSend, { returning: "minimal" });
    if (error) {
      console.error(error);
      return err("Failed to create new reward");
    }
    return ok(undefined);
  };

  // TODO: Ensure that we cannot hit a situation where there are no pointTypes defined.

  return (
    <ErrorOrLoadingWrapper data={points} error={error}>
      {(innerPoints) => (
        <Formik
          initialValues={{
            name: "",
            description: "",
            point_modifier: 5,
            points_id: innerPoints[0].id,
            partnership_id: props.partnershipID,
          }}
          onSubmit={async (values, { setSubmitting }) => {
            const res = await saveNewReward(values);
            setSubmitting(false);

            res.match(
              () => {
                props.newAlert({
                  level: "success",
                  message: "Successfully created point type",
                  timeout: 3,
                });

                // Refresh the displayed rewards
                mutate(REWARDS);
              },
              (err) => {
                props.newAlert({ level: "error", message: err, timeout: 3 });
              }
            );

            props.close();
          }}
        >
          {(formik) => (
            <BaseForm>
              <FormSection>
                <TextField name="name" label="Name" />

                <TextField label="Description" name="description" />

                <div className="mt-6">
                  <FormLabel>Cost</FormLabel>
                  <div className="flex space-x-2">
                    <NumberField
                      className="w-20 shrink"
                      name="point_modifier"
                      numberFormat={{
                        decimalScale: 0,
                      }}
                    />
                    <SelectPointTypeField
                      className="grow"
                      name="points_id"
                      pointTypes={innerPoints}
                    />
                  </div>
                </div>
              </FormSection>

              <SaveCancel
                isSubmitting={formik.isSubmitting}
                close={props.close}
              />
            </BaseForm>
          )}
        </Formik>
      )}
    </ErrorOrLoadingWrapper>
  );
};
