import { Formik } from "formik";
import React from "react";
import { supabase } from "../../../supabase";
import { CenteredForm } from "../../CenteredForm";
import { PrimaryButton } from "../../elements/Buttons";
import { EmailField } from "../../elements/fields/EmailField";
import { PasswordField } from "../../elements/fields/PasswordField";
import { BaseForm, FormSection } from "../../elements/Form";

export const Signup: React.FC<{}> = (props) => {
  const [message, setMessage] = React.useState("");

  return (
    <CenteredForm
      header={
        <>
          <img
            className="mx-auto h-12 w-auto"
            src="/logo512-nobg.png"
            alt="HappySub"
          />
          <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
            Create Account
          </h2>
        </>
      }
    >
      <Formik
        initialValues={{ email: "", password: "" }}
        onSubmit={async (values) => {
          const { error } = await supabase.auth.api.signUpWithEmail(
            values.email,
            values.password
          );

          // TODO: Check what happens if an account already exists.

          if (error) {
            console.error(error);
            setMessage("Failed to set password");
          } else {
            setMessage("Password updated");
          }
        }}
      >
        {(formik) => (
          <BaseForm>
            <FormSection>
              <EmailField name="email" label="Email address" />
              <PasswordField name="password" label="Password" />
            </FormSection>

            {message && <p>{message}</p>}

            <div>
              <PrimaryButton
                disabled={formik.isSubmitting}
                className="w-full"
                type="submit"
              >
                Send Confirmation Email
              </PrimaryButton>
            </div>
          </BaseForm>
        )}
      </Formik>
    </CenteredForm>
  );
};
