import { Fragment, useState } from "react";
import { Listbox, Transition } from "@headlessui/react";
import { CheckIcon, SelectorIcon } from "@heroicons/react/solid";
import { DEFAULT_NAME, Field, ViewProps } from "./Field";
import { useField } from "formik";
import { classNames } from "../../../css";
import { PointType } from "../../pages/points/Points";
import { PointIcon } from "../../pages/points/PointIcon";

export const SelectPointTypeField: React.FC<
  {
    pointTypes: PointType[];
  } & ViewProps
> = ({ pointTypes, ...fieldProps }): JSX.Element => {
  const [, meta, helpers] = useField(fieldProps.name || DEFAULT_NAME);
  const selected = pointTypes.find((p) => p.id === meta.value);

  // NOTE: Not properly using renderProps, but likely not an issue because this
  // is a select field so it cannot have invalid input.

  if (!selected) {
    return <p>Unable to load points</p>;
  }

  return (
    <Field {...fieldProps}>
      {(renderProps) => (
        <Listbox value={meta.value} onChange={helpers.setValue}>
          {({ open }) => (
            <div className="relative mt-1">
              <Listbox.Button className="relative w-full cursor-default rounded-md border border-gray-300 bg-white py-2 pl-3 pr-10 text-left shadow-sm focus:border-indigo-500 focus:outline-none focus:ring-1 focus:ring-indigo-500 sm:text-sm">
                <span className="flex items-center">
                  <PointIcon
                    color={selected.color}
                    shape={selected.shape}
                    className="h-5 w-5 flex-shrink-0"
                  />
                  <span className="ml-3 block truncate">{selected.name}</span>
                </span>
                <span className="pointer-events-none absolute inset-y-0 right-0 ml-3 flex items-center pr-2">
                  <SelectorIcon
                    className="h-5 w-5 text-gray-400"
                    aria-hidden="true"
                  />
                </span>
              </Listbox.Button>

              <Transition
                show={open}
                as={Fragment}
                leave="transition ease-in duration-100"
                leaveFrom="opacity-100"
                leaveTo="opacity-0"
              >
                <Listbox.Options className="absolute z-10 mt-1 max-h-56 w-full overflow-auto rounded-md bg-white py-1 text-base shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none sm:text-sm">
                  {pointTypes.map((pointType, i) => (
                    <Listbox.Option
                      key={pointType.id}
                      className={({ active }) =>
                        classNames(
                          active ? "bg-indigo-600 text-white" : "text-gray-900",
                          "relative cursor-default select-none py-2 pl-3 pr-9"
                        )
                      }
                      value={pointType.id}
                    >
                      {({ selected, active }) => (
                        <>
                          <div className="flex items-center">
                            <PointIcon
                              color={pointType.color}
                              shape={pointType.shape}
                              className="h-5 w-5 flex-shrink-0"
                            />
                            <span
                              className={classNames(
                                selected ? "font-semibold" : "font-normal",
                                "ml-3 block truncate"
                              )}
                            >
                              {pointType.name}
                            </span>
                          </div>

                          {selected ? (
                            <span
                              className={classNames(
                                active ? "text-white" : "text-indigo-600",
                                "absolute inset-y-0 right-0 flex items-center pr-4"
                              )}
                            >
                              <CheckIcon
                                className="h-5 w-5"
                                aria-hidden="true"
                              />
                            </span>
                          ) : null}
                        </>
                      )}
                    </Listbox.Option>
                  ))}
                </Listbox.Options>
              </Transition>
            </div>
          )}
        </Listbox>
      )}
    </Field>
  );
};
