import { MinusCircleIcon, PlusCircleIcon } from "@heroicons/react/outline";
import React from "react";
import useSWR, { useSWRConfig } from "swr";
import {
  POINTS,
  REWARDS,
  TASKS_COMPLETION,
  TASKS_PERIOD,
} from "../../../cacheKeys";
import useCounter from "../../../hooks/useCounter";
import useDebounce from "../../../hooks/useDebounce";
import { supabase } from "../../../supabase";
import { Alert } from "../../elements/Alert";
import { ErrorOrLoadingWrapper } from "../../elements/ErrorLoadingWrapper";
import { Grid } from "../../elements/Grid";
import { PageHeading } from "../../elements/PageHeading";
import { PointIcon } from "./PointIcon";

// TODO: Add different reward shapes too

export type PointType = {
  id: number;
  name: string;
  color: string;
  shape: string;
  value: number;
};

// TODO: Explore using listeners to be updated if someone else modifies the value
// https://supabase.com/docs/reference/javascript/subscribe

export const getPoints = async (cacheKey: string): Promise<PointType[]> =>
  supabase
    .from<PointType>("points")
    .select("id, name, color, shape, value")
    .order("id")
    .throwOnError()
    .then(({ data }) => data || []);

export const Points: React.FC<{
  partnershipID: number;
  newAlert: (newAlert: Alert) => void;
}> = (props): JSX.Element => {
  const { data: points, error } = useSWR(POINTS, getPoints);

  // TODO: Create a default point type when a user logs in for the first time
  //       This would probably be best accomplished while the user is
  //       going through the initial profile setup.

  // TODO: Add intro.js

  return (
    <>
      <PageHeading>Points</PageHeading>
      <p className="mt-2 text-sm text-gray-500">
        Create unique point types, then use as rewards for tasks.
      </p>

      {/* TODO: Have a nice empty state when there are no tasks */}
      <ErrorOrLoadingWrapper data={points} error={error}>
        {(innerData) =>
          innerData.length > 0 ? (
            <div className="py-4">
              <Grid
                component={PointsCard}
                newAlert={props.newAlert}
                data={innerData}
              />
            </div>
          ) : (
            <p>No points</p>
          )
        }
      </ErrorOrLoadingWrapper>
    </>
  );
};

const PointsCard: React.FC<{
  data: PointType;
  newAlert: (newAlert: Alert) => void;
}> = (props): JSX.Element => {
  const { mutate } = useSWRConfig();
  const [unsentChanges, { inc, dec }] = useCounter(0);
  const value = props.data.value + unsentChanges;

  useDebounce(
    async () => {
      if (unsentChanges === 0) {
        return;
      }

      const localUnsent = unsentChanges;
      const newValue = props.data.value + localUnsent;

      supabase
        .from("points")
        .update({ value: newValue }, { returning: "minimal" })
        .match({ id: props.data.id })
        .then(({ error }) => {
          if (error) {
            console.error(error);
            // TODO: Display a warning alert
          }

          mutate(POINTS, (allPoints: PointType[]) =>
            allPoints.map((point) => {
              if (point.id === props.data.id) {
                point.value = newValue;
              }
              return point;
            })
          );
          mutate(REWARDS);
          mutate(TASKS_COMPLETION);
          mutate(TASKS_PERIOD);
          dec(localUnsent);
        });
    },
    500,
    [unsentChanges]
  );

  // TODO: Verify policy prevents sub from changing points
  // TODO: Only show point ajustment buttons for dom
  // TODO: Prevent double click highlighting

  return (
    <div className="flex w-full flex-row divide-x divide-gray-200">
      <div className="flex flex-1 items-center px-4">
        <PointIcon
          className="h-6 w-6"
          color={props.data.color}
          shape={props.data.shape}
        />
      </div>

      <div className="flex w-full items-center justify-between space-x-6 p-6">
        <h3 className="text-md truncate font-medium text-gray-900">
          {props.data.name}
        </h3>
        <div className="flex items-center">
          <MinusCircleIcon
            className="h-6 w-6 cursor-pointer text-gray-400 hover:text-gray-700"
            onClick={() => dec(1)}
          />
          <div className="px-2 text-lg">{value}</div>
          <PlusCircleIcon
            className="h-6 w-6 cursor-pointer text-gray-400 hover:text-gray-700"
            onClick={() => inc(1)}
          />
        </div>
      </div>
    </div>
  );
};
