import { isObject, useField } from "formik";
import { map } from "lodash";
import React, { ElementType, ReactElement, ReactNode } from "react";
import { classNames } from "../../../css";
import { FormLabel } from "../Form";

export interface RenderProps {
  autoFocus?: boolean;
  className: string;
  disabled?: boolean;
  id?: string;
  placeholder?: string;
}

export type FieldValue = any;

export type OnChange = (value: FieldValue) => void;

export const DEFAULT_NAME = "field";

export interface ViewProps {
  autoFocus?: boolean;
  className?: string;
  description?: string;
  disabled?: boolean;
  fieldClassName?: string;
  hint?: ReactNode;
  label?: string;
  name?: string;
  placeholder?: string;
}

export const Field: React.FC<
  {
    children: (props: RenderProps) => ReactNode;
  } & ViewProps
> = ({
  autoFocus,
  className,
  description,
  disabled,
  fieldClassName,
  hint,
  label,
  name,
  placeholder,
  children,
  ...props
}) => {
  const [, meta] = useField(name || DEFAULT_NAME);

  /**
   * Renders a label using the stock label styling
   */
  const renderLabel = (): ReactNode | null =>
    label ? (
      <FormLabel>
        <div className="mb-1 flex items-center justify-between">
          <span
            className={meta.touched && meta.error ? "field-label--invalid" : ""}
          >
            {label}
          </span>
          {hint && (
            <span className="text-sm leading-none text-gray-500">{hint}</span>
          )}
        </div>
      </FormLabel>
    ) : null;

  /**
   * Renders description under label
   */
  const renderDescription = (): ReactElement | null =>
    description ? <p className="mb-2 text-xs">{description}</p> : null;

  /**
   * Renders any errors
   */
  const renderErrors = (): ReactNode => {
    if (!meta.touched || !meta.error) {
      return null;
    }
    return isObject(meta.error) ? (
      map(meta.error, (error, field) => (
        <span className="error">
          {field}: {error}
        </span>
      ))
    ) : (
      <span className="error">{meta.error}</span>
    );
  };

  /**
   * We use the "function as child component" pattern here to make Field
   * highly composable. Field should probably not be used directly in most cases,
   * but instead implement a new component like DropdownField which serves a
   * specific purpose and simplifies the API.
   */
  const renderProps: RenderProps = {
    className: classNames(
      fieldClassName || "",
      "w-full form-input sm:text-sm sm:text-sm shadow-sm border-gray-300 rounded-md block focus:outline-none focus:border-indigo-500 focus:ring-indigo-500",
      meta.touched && meta.error
        ? "border-red-300 text-red-900 focus:ring focus:ring-red-500"
        : "",
      disabled
        ? "cursor-not-allowed grayscale bg-gray-100 pointer-events-none"
        : ""
    ),
    disabled,
    id: name,
    placeholder,
    autoFocus,
  };

  return (
    <div className={classNames("mt-6 block", className || "")}>
      {renderLabel()}
      {renderDescription()}
      {children(renderProps)}
      {renderErrors()}
    </div>
  );
};
