import { ReactElement, ReactNode } from "react";

/**
 * ErrorLoadingWrapper provides a nice loading and error page intended to be
 * used in conjunction with SWR outputs. The child node is a function which
 * accepts the data after having verified that it is not undefined. It may
 * however still be empty.
 */
export const ErrorOrLoadingWrapper = <T,>(
  props: React.PropsWithChildren<{
    error: any;
    data: T | undefined;
    children: (data: T) => ReactNode;
  }>
): ReactElement => {
  if (props.error) {
    console.error(props.error);
    return <p>Error loading data</p>;
  } else if (props.data === undefined) {
    return <p>Loading...</p>;
  }
  return <>{props.children(props.data)}</>;
};
