type UserInfo = {
  userID: string;
  isSub: boolean;
  username: string;
  partnershipID: number;
};

type DBUserInfoProps = {
  user_id: string;
  is_sub: boolean;
  partnership_id: number;
  profiles: {
    username: string;
  };
};

const getUserInfo = async (
  cacheKey: string,
  userID: string
): Promise<UserInfo> =>
  supabase
    .from<DBUserInfoProps>("user_partnerships")
    .select(`partnership_id, is_sub, profiles(username)`)
    .eq("user_id", userID)
    .single()
    .then(({ data, error }): UserInfo => {
      if (error) {
        throw error;
      } else if (!data) {
        throw new Error("Falied to retrieve user data");
      }
      return {
        userID: userID,
        isSub: data.is_sub,
        username: data.profiles.username,
        partnershipID: data.partnership_id,
      };
    });

