import { Formik } from "formik";
import React from "react";
import { supabase } from "../../../supabase";
import { PrimaryButton } from "../../elements/Buttons";
import { EmailField } from "../../elements/fields/EmailField";
import { PasswordField } from "../../elements/fields/PasswordField";
import { BaseForm, FormSection } from "../../elements/Form";

export const SignIn: React.FC<{}> = (props) => {
  const [message, setMessage] = React.useState("");

  return (
    <div className="flex min-h-full items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
      <div className="w-full max-w-md space-y-8">
        <div>
          <img
            className="mx-auto h-12 w-auto"
            src="/logo512-nobg.png"
            alt="HappySub"
          />
          <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
            Sign in to your account
          </h2>
          <p className="mt-2 text-center text-sm text-gray-600">
            Or{" "}
            <a
              href="/signup"
              className="font-medium text-indigo-600 hover:text-indigo-500"
            >
              create a new account
            </a>
          </p>
        </div>
        <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
          <Formik
            initialValues={{ email: "", password: "" }}
            onSubmit={async (values, { setSubmitting }) => {
              const { user, session, error } = await supabase.auth.signIn(
                values
              );
              if (error?.message) {
                setMessage(error.message);
              }

              setSubmitting(false);
            }}
          >
            {(formik) => (
              <BaseForm>
                <div className="flex items-center justify-center">
                  {message && <p className="text-red-500">{message}</p>}
                </div>

                <FormSection>
                  <EmailField name="email" label="Email address" />
                  <PasswordField name="password" label="Password" />
                </FormSection>

                <div className="flex items-center justify-end">
                  <div className="text-sm">
                    <a
                      href="/forgot-password"
                      className="font-medium text-indigo-600 hover:text-indigo-500"
                    >
                      Forgot your password?
                    </a>
                  </div>
                </div>

                <div>
                  {/* TODO: Disable while submitting */}
                  <PrimaryButton className="w-full" type="submit">
                    Sign in
                  </PrimaryButton>
                </div>
              </BaseForm>
            )}
          </Formik>
        </div>
      </div>
    </div>
  );
};
