import { useField } from "formik";
import React from "react";
import { Switch } from "@headlessui/react";
import { Field, ViewProps, DEFAULT_NAME } from "./Field";
import { classNames } from "../../../css";
import { FormLabel } from "../Form";

/**
 * Wrapper for Field which uses the react-number-format component
 */
export const ToggleField: React.FC<
  {
    inlineLabel: string;
  } & ViewProps
> = ({ inlineLabel, ...fieldProps }): JSX.Element => {
  const [, meta, helpers] = useField(fieldProps.name || DEFAULT_NAME);

  return (
    <Field {...fieldProps}>
      {(renderProps) => (
        <div className="relative mt-1 rounded-md">
          <Switch.Group as="div" className="flex items-center">
            <Switch.Label as="span" className="mr-3 grow">
              <FormLabel>{inlineLabel}</FormLabel>
            </Switch.Label>
            <Switch
              checked={meta.value}
              onChange={helpers.setValue}
              className={classNames(
                meta.value ? "bg-indigo-600" : "bg-gray-200",
                "relative inline-flex h-6 w-11 flex-shrink-0 cursor-pointer rounded-full border-2 border-transparent transition-colors duration-200 ease-in-out focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
              )}
            >
              <span
                aria-hidden="true"
                className={classNames(
                  meta.value ? "translate-x-5" : "translate-x-0",
                  "pointer-events-none inline-block h-5 w-5 transform rounded-full bg-white shadow ring-0 transition duration-200 ease-in-out"
                )}
              />
            </Switch>
          </Switch.Group>
        </div>
      )}
    </Field>
  );
};
