import { Diamond } from "../../resources/diamond";

export const PointIcon: React.FC<{
  className?: string;
  color: string;
  shape: string;
}> = (props): JSX.Element => {
  const fill = (() => {
    switch (props.color) {
      case "blue":
        return "fill-blue-500";
      case "red":
        return "fill-red-500";
      case "green":
        return "fill-green-500";
      case "yellow":
        return "fill-yellow-500";
      default:
        return "fill-black";
    }
  })();

  const shape = <Diamond fill={fill} />;

  return <div className={props.className}>{shape}</div>;
};
