import React, { useEffect } from "react";
import { LoggedOutHome } from "./components/pages/LoggedOutHome";
import { LoggedInHome } from "./components/pages/LoggedInHome";
import { Alert, Alerts } from "./components/elements/Alert";
import { useSWRConfig } from "swr";
import { supabase, useSession } from "./supabase";
import { LOGIN } from "./cacheKeys";

export const App: React.FC<{}> = (): JSX.Element => {
  const session = useSession();
  const { mutate } = useSWRConfig();

  useEffect(() => {
    supabase.auth.onAuthStateChange((_event, session) => {
      console.log("Auth change...");
      console.log(_event);

      if (_event === "PASSWORD_RECOVERY") {
        window.location.href = "/password-reset";
      }

      mutate(LOGIN);
    });
  }, [mutate]);

  // Alerts
  const [alerts, setAlerts] = React.useState<Alert[]>([]);
  const newAlert = (newAlert: Alert) => {
    setAlerts([...alerts, newAlert]);
  };
  const AlertComp = <Alerts alerts={alerts} setAlerts={setAlerts} />;

  return session.error ? (
    <p>Error Loading</p>
  ) : session.session && session.user ? (
    <LoggedInHome user={session.user} alerts={AlertComp} newAlert={newAlert} />
  ) : (
    <>
      {/* Display alerts somewhere */}
      <LoggedOutHome />
    </>
  );
};
