import React from "react";
import {
  CheckCircleIcon,
  InformationCircleIcon,
  XCircleIcon,
  XIcon,
} from "@heroicons/react/solid";
import { useInterval } from "../../hooks/useInterval";

export type Alert = {
  level: "info" | "success" | "error";
  message: string;
  timeout?: number; // in seconds
};

export const Alerts: React.FC<{
  alerts: Alert[];
  setAlerts: React.Dispatch<React.SetStateAction<Alert[]>>;
}> = (props): JSX.Element => {
  useInterval(
    () => {
      // Decrement the timeout, then remove expired alerts
      props.setAlerts(
        props.alerts
          .map((a) => (a.timeout ? { ...a, timeout: a.timeout - 1 } : a))
          .filter((a) => a.timeout === undefined || a.timeout > 0)
      );
    },
    props.alerts.length ? 1000 : null
  );

  return (
    <>
      {props.alerts.map((alert: Alert, i): JSX.Element => {
        const close = () => {
          props.setAlerts(props.alerts.filter((_, j) => i !== j));
        };
        switch (alert.level) {
          case "info":
            return (
              <InfoAlert key={alert.message} alert={alert} close={close} />
            );
          case "success":
            return (
              <SuccessAlert key={alert.message} alert={alert} close={close} />
            );
          default:
            return (
              <ErrorAlert key={alert.message} alert={alert} close={close} />
            );
        }
      })}
    </>
  );
};

const InfoAlert: React.FC<{
  alert: Alert;
  close: () => void;
}> = (props): JSX.Element => (
  <div className="rounded-md bg-blue-50 p-4">
    <div className="flex">
      <div className="flex-shrink-0">
        <InformationCircleIcon
          className="h-5 w-5 text-blue-400"
          aria-hidden="true"
        />
      </div>
      <div className="ml-3">
        <h3 className="text-sm font-medium text-blue-800">
          {props.alert.message}
        </h3>
      </div>
      <div className="ml-auto pl-3">
        <div className="-mx-1.5 -my-1.5">
          <button
            type="button"
            onClick={props.close}
            className="inline-flex rounded-md bg-blue-50 p-1.5 text-blue-500 hover:bg-blue-100 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-offset-2 focus:ring-offset-blue-50"
          >
            <span className="sr-only">Dismiss</span>
            <XIcon className="h-5 w-5" aria-hidden="true" />
          </button>
        </div>
      </div>
    </div>
  </div>
);

