import { useField } from "formik";
import NumberFormat, { NumberFormatProps } from "react-number-format";
import React from "react";
import { Field, ViewProps, DEFAULT_NAME } from "./Field";

/**
 * Wrapper for Field which uses the react-number-format component
 */
export const NumberField: React.FC<
  {
    numberFormat?: NumberFormatProps;
    prefix?: string;
    suffix?: string;
  } & ViewProps
> = ({ numberFormat, prefix, suffix, ...fieldProps }): JSX.Element => {
  const [, meta, helpers] = useField(fieldProps.name || DEFAULT_NAME);

  return (
    <Field {...fieldProps}>
      {(renderProps) => (
        <div className="relative mt-1 rounded-md shadow-sm">
          {prefix && (
            <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center pr-3">
              <span className="text-gray-500 sm:text-sm">{prefix}</span>
            </div>
          )}
          <NumberFormat
            value={meta.value}
            onValueChange={({ value }) => helpers.setValue(value)}
            {...renderProps}
            {...numberFormat}
            data-testid="input"
          />
          {suffix && (
            <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center pr-3">
              <span className="text-gray-500 sm:text-sm">{suffix}</span>
            </div>
          )}
        </div>
      )}
    </Field>
  );
};
