import { useField } from "formik";
import React from "react";
import { Field, ViewProps, DEFAULT_NAME } from "./Field";

/**
 * Wrapper for Field which uses an email input
 */
export const EmailField: React.FC<ViewProps> = (fieldProps): JSX.Element => {
  const [, meta, helpers] = useField(fieldProps.name || DEFAULT_NAME);

  return (
    <Field {...fieldProps}>
      {(renderProps) => (
        <input
          type="email"
          autoComplete="email"
          value={meta.value}
          onChange={(e) => helpers.setValue(e.target.value)}
          {...renderProps}
        />
      )}
    </Field>
  );
};
